import {api} from '../../api/api.js'
export default {
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			top: getApp().globalData.top,
			session_id:getApp().globalData.session_id,
			uid:getApp().globalData.uid,
			msg:"发送",
			tiemer:null,
			active:false,
			tell:'',
			pwd:'',
			pwds:'',
			yzm:''
		}
	},
	onLoad() {
		this.getInfo();
	},
	methods: {
		toBack() {
			uni.navigateBack();
		},
		send() {
			this.active = true;
			this.msg = 60;
			this.tiemer = setInterval(()=>{
				this.msg--;
				if(this.msg === 0) {
					clearInterval(this.tiemer);
					this.msg = '发送';
					this.active = false;
					return;
				}
			},1000)
			uni.request({
				url:api.set_pay_code,
				data: {
					uid:this.uid,
					session_id:this.session_id
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					console.log(res);
				}
			})
		},
		getInfo() {
			uni.request({
				url:api.info,
				data: {
					uid:this.uid,
					session_id:this.session_id
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					if(res.data.code === "success") {
						this.tell = res.data.result.mobile;
					}else {
						console.log(res);
					}
				}
			})
		}
	}
}
